<template>
  <nav class="flex items-center justify-between flex-wrap bg-indigo-900 p-6">
    <div class="flex items-center flex-shrink-0 text-white mr-6">
      <span class="font-semibold text-2xl tracking-tight">Cafe Bang Mul</span>
    </div>
    <div class="block lg:hidden">
      <button @click="toggleMenu" class="flex items-center px-3 py-2 border rounded text-teal-200 border-teal-400 hover:text-white hover:border-white">
        <svg class="fill-current h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title>
          <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
        </svg>
      </button>
    </div>
    <div :class="['w-full block flex-grow lg:flex lg:items-center lg:w-auto', !isMenuOpen && 'hidden']">
      <div class="text-sm lg:flex-grow">
        <a href="javascript:void(0)" @click="category('')" class="inline-block text-sm px-4 py-2 mx-4 leading-none border rounded text-white border-white hover:border-transparent hover:text-teal-500 hover:bg-white mt-4 lg:mt-0">All</a>
        <a href="javascript:void(0)" @click="category('tea')" class="inline-block text-sm px-4 py-2 mx-4 leading-none border rounded text-white border-white hover:border-transparent hover:text-teal-500 hover:bg-white mt-4 lg:mt-0">Teh</a>
        <a href="javascript:void(0)" @click="category('coffee')" class="inline-block text-sm px-4 py-2 mx-4 leading-none border rounded text-white border-white hover:border-transparent hover:text-teal-500 hover:bg-white mt-4 lg:mt-0">Kopi</a>
        <a href="javascript:void(0)" @click="category('juice')" class="inline-block text-sm px-4 py-2 mx-4 leading-none border rounded text-white border-white hover:border-transparent hover:text-teal-500 hover:bg-white mt-4 lg:mt-0">Juice</a>
        <a href="javascript:void(0)" @click="category('snack')" class="inline-block text-sm px-4 py-2 mx-4 leading-none border rounded text-white border-white hover:border-transparent hover:text-teal-500 hover:bg-white mt-4 lg:mt-0">Snack</a>
      </div>
      <div>
        <div class="block mt-4 lg:inline-block lg:mt-0 text-white mr-10">
          Hello, {{ username }}
        </div>
        <a @click="logout" href="javascript:void(0)" class="block mt-4 lg:inline-block lg:mt-0 text-teal-200 hover:text-white mr-4">
          Logout
        </a>
      </div>
    </div>
  </nav>
</template>

<script>
  import Cookies from 'js-cookie'

  export default {
    name: "Navigation.vue",

    data() {
      return {
        isMenuOpen: false,
        username: Cookies.get('username'),
      }
    },

    methods: {
      toggleMenu() {
        this.isMenuOpen = ! this.isMenuOpen
      },

      category(type) {
        this.$store.dispatch('updateProductList', type)
      },

      logout() {
        Cookies.remove('username')
        this.$router.push('/login')
      }
    }
  }
</script>

